$loaderSize: 120px;

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid orange;
  border-radius: 50%;
  width: $loaderSize;
  height: $loaderSize;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
